.header-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
  }
  
  .header-logo {
    max-height: 50px;
  }
  
  /* Menu par d√©faut (desktop) */
  .menu {
    display: flex;
    list-style: none;
    gap: 20px;
  }
  
  /* Masquer le menu sur mobile */
  .menu-toggle {
    display: none;
    font-size: 24px;
    background: none;
    border: none;
    cursor: pointer;
  }
  
  /* Responsive mobile */
  @media (max-width: 768px) {
    .menu {
      display: none;
      flex-direction: column;
      gap: 10px;
      position: absolute;
      top: 60px;
      right: 20px;
      background: white;
      padding: 10px;
      border: 1px solid #ddd;
      box-shadow: 0 2px 8px rgba(0,0,0,0.2);
    }
  
    .menu.show {
      display: flex;
    }
  
    .menu-toggle {
      display: block;
    }
  }
  